<template>
  <div class="admin-book-wrap">
    <el-alert
      title=""
      type="info"
      show-icon>
      <div>
        <p>
          ID
          <router-link class="red" :to="{name:'addNewBook'}"></router-link>
        </p>
      </div>
    </el-alert>
   
    <el-row class="mbt20">
      <el-col :xs="20" :sm="16" :md="12" :lg="9" :xl="6">
        <el-input placeholder="" v-model="keywords" class="input-with-select" @keyup.enter.native="searchBook">
          <el-select v-model="selectType" slot="prepend" placeholder="">
            <el-option label="  " value="bookName"></el-option>
            <el-option label=" " value="writerName"></el-option>
            <el-option label="ID" value="bookId"></el-option>
            <el-option label="ID" value="bookWriterId"></el-option>
            <el-option label="" value="userPhone"></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search" @click="searchBook"></el-button>
        </el-input>
      </el-col>
    </el-row>
    
    <el-row>
      <el-table
        ref="multipleTable"
        :data="bookList.list"
        border
        @row-click="handleRowClick"
        @selection-change="handleSelectionChange"
        style="width: 100%">
        <el-table-column
          type="selection"
          aligin="center">
        </el-table-column>
        <el-table-column
          align="center"
          prop="bookId"
          label="ID">
        </el-table-column>
        
        <el-table-column
          prop="bookName"
          label="">
        </el-table-column>
        
        <el-table-column
          prop="writerName"
          label="">
        </el-table-column>
        
        <el-table-column
          align="center"
          prop="bookStatus"
          label="">
          <template slot-scope="scope">
            <span class="el-dropdown-link" :class="!scope.row.bookStatus?'success':'primary'">
              {{!scope.row.bookStatus?'':''}}
            </span>
          </template>
        </el-table-column>
        
        <el-table-column
          align="center"
          prop="bookCheckStatus"
          label="">
          <template slot-scope="scope">
            <span class="el-dropdown-link" :class="scope.row.bookCheckStatus?'success':'primary'">
              {{scope.row.bookCheckStatus?"":""}}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="bookStatus"
          label="">
          <template slot-scope="scope">
            <span class="el-dropdown-link" :class="scope.row.bookCheckStatus===2?'success':'primary'">
              {{scope.row.bookCheckStatus==2?"":""}}
            </span>
           
          </template>
        </el-table-column>
        
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span >{{ scope.row.bookCreatedTime | time('long') }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <span>{{ scope.row.lastUpdateTime | time('long') }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="bookWorldCount"
          label=""
          align="center">
        </el-table-column>
        
        <el-table-column
          class="last-column"
          v-if="$store.state.userInfo && $store.state.userInfo.adminRolemenuanduserrole.updates"
          label=""
          align="center">
          <template slot-scope="scope">
            <a href="javascript:0;"></a>
          </template>
        </el-table-column>
      </el-table>
      
      <el-pagination
        class="mbt20"
        background
        @current-change="handleCurrentChange"
        :current-page="bookList.pageNum"
        :page-size="bookList.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="bookList.total">
      </el-pagination>
    </el-row>
    
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    components:{
      'pic-cropper': () => require('../../components/img_upload.vue')
    },
    data(){
      return{
        dialogTableVisible:false,
        bookList:{},
        updateData:{},
        multipleSelection:[],
        keywords:'',
        selectType:'bookName'
      }
    },
    methods:{
      getBookList(){
        let searchValue = {
          page:this.$route.params.page,
          orderParemeter:'bookId'
        };
        let val = this.$http.trim(this.keywords);
        if(this.selectType && val){
          if((this.selectType==='bookId' || this.selectType==='bookWriterId') && !Number(val)){
            this.$message({message:'ID',type:'warning'});
            return false
          }
          searchValue[this.selectType] = val;
          this.bookList = {}
        }
        this.$ajax("/admin/getBookInfoList",searchValue,res=>{
          if(res.returnCode===200){
            this.bookList = res.data
          }
        })
      },
      searchBook(){
        if(Number(this.$route.params.page)!==1){
          this.$router.push({params:{page:1}})
        }else {
          this.getBookList()
        }
      },
//        
      toggleSelection(){
        if(this.multipleSelection.length){
          let id = [];
          this.multipleSelection.forEach((item)=>{
            id.push(item.bookId)
          });
          this.$confirm(''+id.length+', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$ajax('/admin/bookBatchDelete',{
              bookid:id.toString()
            },res=>{
              if(res.returnCode===200){
                this.$message({message:'',type:'success'});
                this.getBookList()
              }
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
          
        }else {
          this.$message({message:' ',type:'warning'})
        }
      },
      handleRowClick(row, event, column){
        if(column.label!==''){
          this.$refs.multipleTable.toggleRowSelection(row);
        }
        
      },
      
      handleSelectionChange(val){
        this.multipleSelection = val
      },
      handleClick(val,data){
        if(val==='a'){
          //              
          this.$router.push({path:'/book_chapter_list/'+data.bookId})
        }else if(val==='b'){
          //              
          this.$router.push({path:'/book_detail/'+data.bookId})
          
        }else if(val==='c'){
          //              
          this.$router.push({path:'/add_new_chapter/'+data.bookId})
          
        }else if(val==='d'){
          this.toggleSelection();
          //              
//            this.$router.push({path:'/admin/book_list/'+data.bookId})
        
        }
      },
      handleCurrentChange(page){
        this.$router.push({params:{page:page}})
      },

//        
      bookChangeState(val,data){
        let state = data.bookCheckStatus,status = data.bookStatus;
        if(val==='a'){
//              
          state = 1
        }else if(val==='b'){
//              
          if(data.bookCheckStatus===0){
            this.$message({message:'',type:'warning'});
            return false
          }else {
            state = 2
          }
        }else if(val==='c'){
//              
          if(data.bookCheckStatus<=1){
            this.$message({message:'',type:'warning'});
            return false;
          }else {
            state = 1
          }
        }else if(val==='d'){
          status = 1
        }else if(val==='e'){
          status = 0
        }else if(val==='f'){
          state = 0
        }
        this.$ajax("/admin/sysbookupdate",{bookId:data.bookId,bookCheckStatus:state,bookAuthorization:data.bookAuthorization,bookStatus:status},res=>{
          if(res.returnCode===200){
            this.$message(res.msg);
            this.getBookList()
          }
        })
      },
      
    },
    created(){
      this.getBookList()
    },
    watch:{
      $route:function () {
        this.getBookList()
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .cover
    width 100%
    cursor pointer
  .cover-preview
    border 1px solid #ddd
    .avatar
      width 200px
      height 200px
</style>



