<template>
  <div class="sys-notice-wrap">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item to="/message/system"></el-breadcrumb-item>
      <el-breadcrumb-item to="/message/system"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-alert
      title=""
      type="info"
      show-icon>
      <div>
        <p>
          APP
        </p>
      </div>
    </el-alert>
    <el-row class="mbt20">
      <el-button type="primary" @click="$router.push({path:'/message/add_message'})"></el-button>
    </el-row>
  
    <el-row class="mbt20">
      <el-table
        ref="multipleTable"
        :data="tableList.list"
        border
        select="row"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column
          type="selection"
          align="center">
        </el-table-column>
        <el-table-column
          prop="id"
          label="ID"
          width="60"
          align="center"
        >
        </el-table-column>
       
        <el-table-column
          prop="title"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="userName"
          label=""
        >
        </el-table-column>
        <el-table-column
          label=""
        >
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.releaseDate | time('long') }}</span>
          </template>
        </el-table-column>
        
        <el-table-column
          label=""
          width="60"
          align="center">
          <template slot-scope="scope">
            <router-link :to="'/message/edit_message/'+scope.row.id"></router-link>
            <a href="javascript:0;" class="red" @click="delMessage(scope.row.id)"></a>
          </template>
        </el-table-column>
      </el-table>
    
    </el-row>
  </div>
</template>
<script type="text/ecmascript-6">
  export default{
    data(){
      return{
        noticeType:[
          {name:'',value:1},
          {name:'',value:2},
          {name:'',value:3}
        ],
        currentType:1,
        tableList:{}
      };
    },
    methods:{
      getSysMessage(){
        this.$ajax("/admin/getsystemmsg",{startpage:this.$route.params.page},res=>{
          if(res.returnCode===200){
            this.tableList = res.data
          }
        })
      },
      delMessage(id){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$ajax("/admin/sys-deletesystemmsg",{messageid:id},res=>{
              if(res.returnCode===200){
                  this.$message({ message:'',type:'success' });
                  this.getSysMessage()
              }
          })
        })
      }
    },
    created(){
      this.getSysMessage()
    },
    mounted(){
    },
    watch:{
      'currentType':function (val) {
        this.getSysMessage()
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">

</style>



