<template>
<div class="admin-book-wrap">
    <el-breadcrumb class="mbt20" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item to="/book/list"></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
    </el-breadcrumb>
    
    <el-alert title="" type="info" show-icon>
        <div>
            <p> <span class="blue">{{bookInfo.bookName}}</span></p>
            <p>
                
                <router-link v-if="authority.adds" class="red" :to="{name:'bookVolumeList'}"></router-link>
            </p>
            <p>
                <span class="red"></span>
                <i class="el-icon-edit danger"></i>,
                <i class="el-icon-time danger"></i>
                <router-link v-if="authority.adds" class="red" :to="'/add_new_chapter/'+$route.params.bid"></router-link>
            </p>
        </div>
    </el-alert>

    <el-row class="mbt20">
        <el-button v-if="authority.deletes" type="danger" plain @click="toggleRowSelection"></el-button>
    </el-row>

    <el-row>
        <div class="table-wrap">
            <table class="chapter-list" border="0" cellspacing="10" cellpadding="0" width="100%">
                <thead>
                    <tr>
                        <th><el-checkbox @change="selectAll"></el-checkbox></th>
                        <th>ID</th>
                        <th></th>
                        <th align="left"></th>
                        <th>VIP</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <draggable v-model="chapterList" :options="{draggable:'.drag-item'}" element="tbody" :move="dragState" @start="dragStart" @update="dragEnd">
                    <tr class="drag-item" v-for="(item,$index) in chapterList" @click="selected($index)" :key="item.id">
                        <td>
                            <el-checkbox v-model="item.check"></el-checkbox>
                        </td>
                        <td>{{item.id}}</td>
                        <td>{{item.volumeName}}</td>
                        <td align="left">
                            {{item.chapterTitle}}
                            <i v-if="item.whetherPublic" class="el-icon-edit danger"></i>
                            <i v-else-if="nowTime<item.releaseTime" class="el-icon-time danger"></i>
                        </td>
                        <td>
                            <span v-if="item.chapterIsvip" class="danger">VIP</span>
                            <span v-else class="safe"></span>
                        </td>
                        <td>
                            <span v-if="!item.chapterState" class="safe"></span>
                            <span v-else class="danger"></span>
                        </td>
                        <td>
                            {{ item.releaseTime | time('long') }}
                        </td>
                        <td>{{item.chapterLength}}</td>
                        <td>
                            <router-link target="_blank" class="blue" :to="'/edit_chapter/'+item.id"></router-link>
                        </td>
                    </tr>
                </draggable>
            </table>
        </div>
        <el-pagination
            v-if="chapterList.length"
            class="mbt20"
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-size="100"
            layout="total, prev, pager, next, jumper"
            :total="chapterList.length">
        </el-pagination>
    </el-row>
</div>
</template>

<script type="text/ecmascript-6">
import draggable from 'vuedraggable'
export default{

    components:{ draggable },

    data(){
        return{
            checkList:[],
            centerDialogVisible:false,
            chapterList:[],
            cloneList:[],
            updateData:{},
            page:1,
            multipleSelection:[],
            newDrag:true,
            bookInfo:{},
            $routeParams:''
        }
    },

    methods:{

        getChapterList(){
            this.$ajax("/books-adminChapterList/"+this.$route.params.bid,'', res=>{
                let arr = []
                if(res.returnCode===200){
                    res.data.reverse().forEach((item)=>{
                        if(item.resultList.length>0){
                            arr = arr.concat(item.resultList)
                        }
                    })
                    this.chapterList = arr
                }
            },'get')
        },

        getBookInfo(){
            this.$ajax("/book-showBookInfo",{bookid:this.$route.params.bid},res=>{
                if(res.returnCode===200){
                    this.bookInfo = res.data
                }
            })
        },

        dragState(evt){
            let val = evt.draggedContext
            // console.log(val)
            let moveState =  this.cloneList[val.index].volumeId === this.cloneList[val.futureIndex].volumeId && !this.cloneList[val.index].whetherPublic
            if(!moveState){
                if(this.newDrag){
                    if(!this.cloneList[val.index].whetherPublic){
                        this.$message({ message:'', type:'warning', showClose:true })
                    }
                    this.newDrag = false
                }
            }
            return moveState
        },

        // 
        dragStart(evt){
            this.cloneList = JSON.parse(JSON.stringify(this.chapterList))
            this.newDrag = true
            if(this.cloneList[evt.oldIndex].whetherPublic){
                this.$message({ message:'',type:'warning',showClose:true})
                return false
            }
        },

        dragEnd(evt){
            let oldNum = this.cloneList[evt.oldIndex]
            let newNum = this.cloneList[evt.newIndex]
            this.$myLoad()
            this.$ajax("/sys-chapteOrderUpdate",{
                startNum:oldNum.chapterOrder,
                endNum:newNum.chapterOrder,
                bookid:oldNum.bookId,
                volumeid:oldNum.volumeId,
                startChapterid:oldNum.id
            },res=>{
                this.$loading().close()
                if(res.returnCode===200){
                    this.getChapterList()
                    this.$message({ message:'',type:'success',duration:2000,showClose:true})
                }
            })
        },

        // 
        toggleRowSelection(){
            let idList = []
            this.chapterList.forEach((item)=>{
                if(item.check){
                    idList.push(item.id)
                }
            })
            if(idList.length){
                this.$confirm(' '+idList.length+' , ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$ajax("/admin/deletechapter",{
                        chapterid:idList.toString()
                    },res=>{
                        if(res.returnCode===200){
                            this.$message({message:'',type:'success'})
                            this.getChapterList()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    })
                })
            }else {
                this.$message({message:'',type:'warning',showClose:true})
            }
        },

        selected(index){
            let val = this.chapterList[index]
            if(val.check){
                this.$set(val,'check',false)
            }else {
                this.$set(val,'check',true)
            }
        },

        selectAll(event){
            if(event){
                this.chapterList.forEach((item)=>{
                    this.$set(item,'check',true)
                })
            }else{
                this.chapterList.forEach((item)=>{
                    this.$set(item,'check',false)
                })
            }
        },

        handleSelectionChange(val){
            this.multipleSelection = val
        },

        // 
        handleRowClick(row){
            this.$refs.multipleTable.toggleRowSelection(row)
        },
      
        handleClick(val, data){
            if(val==='a'){
                this.$router.push({path:'/edit_chapter/'+data.id})
            }else if(val==='b'){ }
        },
      
        handleCurrentChange(page){
            this.page = page
            this.$router.push({params:{page:page}})
        },

        // 
        bookChangeState(val,data){
            let state = data.bookCheckStatus,status = data.bookStatus
            if(val==='a'){
                // 
                state = 1
            }else if(val==='b'){
                // 
                if(data.bookCheckStatus===0){
                    this.$message({message:'',type:'warning'})
                }else {
                    state = 2
                }
            }else if(val==='c'){
                // 
                if(data.bookCheckStatus<=1){
                    this.$message({message:'',type:'warning'})
                }else {
                    state = 1
                }
            }else if(val==='d'){
                status = 1
            }
            this.$ajax("/admin/sysbookupdate",{
                bookId:data.bookId,
                bookCheckStatus:state,
                bookAuthorization:data.bookAuthorization,
                bookStatus:status
            },res=>{
                if(res.returnCode===200){
                    this.$message(res.msg)
                    this.getBookList()
                }
            })
        }

    },

    created(){
        this.getBookInfo()
        this.getChapterList()
    },

    watch:{
        $route:function () {
            this.getChapterList()
        }
    },

    computed:{
        nowTime:function () {
            let time = new Date().getTime()
            this.$ajax("/sys-getNetWorkDateTime",'',res=>{
                if(res.returnCode===200){
                    time = res.data.beijing
                }
            },'get')
            return time
        },
        authority:function () {
            return this.$store.state.userInfo.adminRolemenuanduserrole?this.$store.state.userInfo.adminRolemenuanduserrole:{}
        }
    },

    filters:{
        checkState:function (val) {
//          for(let k=0,len=this.checkList.length;k<len;k++){
//            if(val==this.checkList[i]){
//                return true
//            }
//          }
        }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    .cover
        width 100%
    .drag-item:hover
        background #fafafa
</style>



