<template>
    <div class="sys-notice-wrap">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item to="/system/notice"></el-breadcrumb-item>
        <el-breadcrumb-item ></el-breadcrumb-item>
      </el-breadcrumb>
      <el-alert
        title=""
        type="info"
        description=""
        show-icon>
        <div>
          <p>
            APP
            <a href="javascript:0;" @click="$clearCache()"></a>
            
          </p>
        </div>
      </el-alert>
      <el-row class="mbt20">
        <el-button type="primary" @click="$router.push({path:'/system/add_notice'})"></el-button>
        <el-button type="primary" class="fr" @click="$clearCache()"></el-button>
      </el-row>
      <el-row :gutter="10" class="mbt20">
        <el-col :xs="18" :sm="12" :md="9" :lg="6" :xl="3">
          <el-select v-model="currentType" placeholder="">
            <el-option
              v-for="(item,index) in noticeType"
              :key="index"
              :label="item.name"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        
      </el-row>
      <el-row class="mbt20">
        <el-table
          ref="multipleTable"
          :data="tableList.list"
          border
          select="row"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column
            type="selection"
            align="center">
          </el-table-column>
          <el-table-column
            prop="id"
            label="ID"
            width="60"
            align="center"
          >
          </el-table-column>
          <el-table-column
            prop="menuName"
            label=""
            width="82"
            align="center">
          </el-table-column>
    
          <el-table-column
            prop="title"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="adminName"
            label=""
          >
          </el-table-column>
          <el-table-column
            label=""
          >
            <template slot-scope="scope">
              <span style="margin-left: 10px">{{ scope.row.releaseDate | time('long') }}</span>
            </template>
          </el-table-column>
         
          <el-table-column
            label=""
            width="60"
            align="center">
            <template slot-scope="scope">
              <a href="javascript:0;"  @click="handleClick(scope.row,'edit')"></a>
              <a href="javascript:0;" class="red" @click="delNotice(scope.row.id)"></a>
            </template>
          </el-table-column>
        </el-table>
        
      </el-row>
    </div>
</template>
<script type="text/ecmascript-6">
    export default{
      data(){
            return{
              noticeType:[
                {name:'',value:1},
                {name:'',value:2},
                {name:'',value:3}
              ],
              currentType:1,
              tableList:{}
            };
        },
      methods:{
        getNotice(){
          this.$ajax("/admin/sys-getNotice",{menuId:this.currentType,page:this.$route.params.page},res=>{
            if(res.returnCode===200){
              this.tableList = res.data
            }
          },'get')
        },
        delNotice(id){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.$ajax('/admin/sys-deleteNotice',{ id:id },res=>{
              if(res.returnCode===200){
                this.$message({message:'',type:'success'});
                this.getNotice();
              }
            })
          })
          
        },
        handleClick(val){
          this.$router.push({path:'/system/edit_notice/'+val.id})
        }
      },
      created(){
        this.getNotice()
      },
      mounted(){
      },
      watch:{
        'currentType':function (val) {
          this.getNotice()
        }
      }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus">

</style>



